import Swiper, { Navigation, Pagination, Thumbs, Autoplay } from "swiper"
import PhotoSwipeLightbox from 'photoswipe/lightbox';
import 'photoswipe/style.css';
import { debounce } from "./functions.js";
// import "swiper/css"

const aboutClinic = new PhotoSwipeLightbox({
    gallery: '.map__gallery-images',
    children: 'a',
    pswpModule: () => import('photoswipe'),
    closeTitle: 'Закрыть фотографию',
    zoomTitle: 'Увеличить фотографию',
    arrowPrevTitle: 'Назад',
    arrowNextTitle: 'Вперед',
});
aboutClinic.init();

function comparisonSwiper() {
    const selector = ".comparison"
    const el = document.querySelector(selector + "__slider")
    if (!el) return
    const options = {
        modules: [Navigation, Pagination],
        loop: true,
        slidesPerView: 1,
        spaceBetween: 20,
        observer: true,
        observeParents: true,
        navigation: {
            nextEl: selector + " .slider-controls-next-button",
            prevEl: selector + " .slider-controls-prev-button",
        },
        pagination: {
            el: selector + " .slider-controls-pagination",
            clickable: true,
            dynamicBullets: true,
        },
        breakpoints: {
            1025: {
                spaceBetween: 51,
                slidesPerView: 2,
                pagination: {
                    dynamicBullets: false,
                },
            },
            700: {
                pagination: {
                    dynamicBullets: false,
                },
            },
        },
        on: {
            afterInit: (swiper) => {
                new LazyLoad({
                    container: swiper.el,
                    cancel_on_exit: false,
                })
            },
        },
    }
    let swiper = new Swiper(el, options)
    window.addEventListener("resize", foo)
    let prevWidth
    function foo() {
        if (window.innerWidth < 700) {
            if (!prevWidth || prevWidth >= 700) {
                reinitSwiper(swiper, el, options)
            }
        } else {
            if (!prevWidth || prevWidth < 700) {
                reinitSwiper(swiper, el, options)
                el.querySelector(".slider-controls-pagination").removeAttribute("style")
            }
        }
        prevWidth = window.innerWidth
    }
}

