function certificationSwiper() {
    const selector = ".certifications"
    const el = document.querySelector(selector + "__slider")
    if (!el) return
    const options = {
        modules: [Navigation, Pagination],
        // switching to dynamic bullets and back 
        // sometimes doesn't work
        // even if you reinit swiper and remove styling
        // navigation stops working properly
        loop: true,
        slidesPerView: 1,
        spaceBetween: 23,
        observer: true,
        observeParents: true,
        resizeObserver: true,
        updateOnWindowResize: true,
        navigation: {
            nextEl: selector + " .slider-controls-next-button",
            prevEl: selector + " .slider-controls-prev-button",
        },
        pagination: {
            el: selector + " .slider-controls-pagination",
            clickable: true,
            dynamicBullets: true,
        },
        breakpoints: {
            1441: {
                slidesPerView: 3,
                pagination: {
                    el: selector + " .slider-controls-pagination",
                    clickable: true,
                    dynamicBullets: false,
                },
            },
            1280: {
                slidesPerView: 2,
                pagination: {
                    el: selector + " .slider-controls-pagination",
                    clickable: true,
                    dynamicBullets: false,
                },
            },
            768: {
                slidesPerView: 2,
                pagination: {
                    el: selector + " .slider-controls-pagination",
                    clickable: true,
                    dynamicBullets: true
                },
            },
        },
        on: {
            afterInit: (swiper) => {
                new LazyLoad({
                    container: swiper.el,
                    cancel_on_exit: false,
                })
            },
        },
    }
    let prevWidth
    let swiper
    const breakpoint = 1280
    const pagination = document.querySelector(selector + " .slider-controls-pagination")
    foo()
    window.addEventListener("resize", debounce(foo))
    function foo() {
        if (window.innerWidth < breakpoint) {
            if (!prevWidth || prevWidth >= breakpoint) {
                reinitSwiper(el, options)
            }
        } else {
            if (!prevWidth || prevWidth < breakpoint) {
                pagination.classList.remove('swiper-bullets-dynamic')
                pagination.removeAttribute('style')
                reinitSwiper(el, options)
            }
        }
        prevWidth = window.innerWidth
    }
    const pswpOptions = {
        gallery: '.certifications__slider',
        children: 'a',
        loop: true,
        counter: true,
        pswpModule: () => import('photoswipe'),
        closeTitle: 'Закрыть фотографию',
        zoomTitle: 'Увеличить фотографию',
        arrowPrevTitle: 'Назад',
        arrowNextTitle: 'Вперед',
    }
    const certifications = new PhotoSwipeLightbox(pswpOptions);
    certifications.init();
    certifications.on('afterInit', () => {
        if (swiper.params.autoplay && swiper.params.autoplay.enabled) {
            swiper.autoplay.stop();
        };
    });
    certifications.on('change', () => {
        swiper.slideTo(certifications.pswp.currIndex, 0, false);
    });
    certifications.on('closingAnimationStart', () => {
        swiper.slideTo(certifications.pswp.currIndex, 0, false);
        if (swiper.params.autoplay && swiper.params.autoplay.enabled) {
            swiper.autoplay.start();
        }
    });
    function reinitSwiper(el, options) {
        if (swiper) {
            swiper.disable()
            swiper.destroy()
            swiper = null
        }
        swiper = new Swiper(el, options)
        swiper.update()
    }
}
function reinitSwiper(swiper, el, options) {
    if (swiper) {
        swiper.disable()
        swiper.destroy(true, true)
        swiper = null
    }
    swiper = new Swiper(el, options)
    swiper.update()
}
function aboutClinicSwiper() {
    const selector = ".about-clinic"
    const slider = document.querySelector(selector + "__slider")
    if (!slider) return
    const thumbOptions = {
        freeMode: true,
        spaceBetween: 8,
        watchSlidesProgress: true,
        loop: true,
        slidesPerView: 3,
        breakpoints: {
            1025: {
                spaceBetween: 16,
                slidesPerView: 5
            },
            769: {
                slidesPerView: 5,
                spaceBetween: 8,
            },
            481: {
                slidesPerView: 5,
            },
            321: {
                slidesPerView: 4,
            },
        },
        on: {
            afterInit: (swiper) => {
                new LazyLoad({
                    container: swiper.el,
                    cancel_on_exit: false,
                })
            },
        },
    }
    let thumbsSlider = new Swiper(selector + " .thumbs-slider", thumbOptions)
    const mainOptions = {
        modules: [Thumbs, Autoplay],
        loop: true,
        slidesPerView: 1,
        speed: 500,
        autoplay: {
            delay: 5000
        },
        thumbs: {
            swiper: thumbsSlider,
        },
        spaceBetween: 30,
        on: {
            afterInit: (swiper) => {
                new LazyLoad({
                    container: swiper.el,
                    cancel_on_exit: false,
                })
            },
        },
    }
    let mainSlider = new Swiper(selector + " .main-slider", mainOptions)
    const pswpOptions = {
        gallery: '.main-slider',
        children: 'a',
        loop: true,
        counter: true,
        pswpModule: () => import('photoswipe'),
        closeTitle: 'Закрыть фотографию',
        zoomTitle: 'Увеличить фотографию',
        arrowPrevTitle: 'Назад',
        arrowNextTitle: 'Вперед',
    }
    const slides = slider.querySelectorAll('.main-slider .swiper-slide:not(.swiper-slide-duplicate)')
    const dataSource = []
    for (const slide of slides) {
        const o = {}
        const link = slide.querySelector('a')
        o.src = link.href
        o.width = link.dataset.pswpWidth
        o.height = link.dataset.pswpHeight
        dataSource.push(o)
    }
    const lightbox = new PhotoSwipeLightbox(pswpOptions);
    lightbox.init();
    lightbox.on('afterInit', () => {
        if (mainSlider.params.autoplay && mainSlider.params.autoplay.enabled) {
            mainSlider.autoplay.stop();
        };
    });
    lightbox.on('change', () => {
        mainSlider.slideTo(lightbox.pswp.currIndex, 0, false);
    });
    lightbox.on('closingAnimationStart', () => {
        mainSlider.slideTo(lightbox.pswp.currIndex, 0, false);
        if (mainSlider.params.autoplay && mainSlider.params.autoplay.enabled) {
            mainSlider.autoplay.start();
        }
    });
}

export function initSliders() {
    //   buildSliders();
    new LazyLoad({
        elements_selector: '.comparison__slider',
        unobserve_entered: true,
        callback_enter: function (swiperElement) {
            comparisonSwiper()
        },
    })
    new LazyLoad({
        elements_selector: '.personal-stories__slider',
        unobserve_entered: true,
        callback_enter: function (swiperElement) {
            storiesSwiper()
        },
    })
    new LazyLoad({
        elements_selector: '.certifications__slider',
        unobserve_entered: true,
        callback_enter: function (swiperElement) {
            certificationSwiper()
        },
    })
    new LazyLoad({
        elements_selector: '.about-clinic__slider',
        unobserve_entered: true,
        callback_enter: function (swiperElement) {
            aboutClinicSwiper()
        },
    })
}
initSliders()
